/* Global Selectors */
* {
  box-sizing: border-box;
}
html {
  height: 100%;
  width: 100%;
  scroll-behavior: smooth;
}
body {
  height: 100%;
  overflow-y: auto;
  width: 100%;
  overflow-x: hidden !important;
}
form{
  max-height: 95%;
}
i{
 color: #ffffff;
 margin: 0;
}
nav {
  object-fit: cover;
}
video {
  position: absolute;
  top: 0;
  left: 0;
  width: 101%;
  height: 100%;
  min-width: 101%;
  min-height: 100%;
  z-index: -1;
  object-position: center;
  object-fit: cover;
}

/* Class Selectors */
.bottom-links{
  background-color: #0F5298;
  width: 500%;
  margin-left: -200%;
  padding: 0;
}
.btn-container {
  text-align: center;
}
.flip-button {
  font-size: 20px;
  font-weight: 900;
  text-transform: uppercase;
  min-width: 250px;
  display: inline-block;
  text-align: center;
  line-height: 40px;
  background: black;
  color: #fff;
  transition: all 0.3s ease-out;
  transform-origin: 50% 0;
  transform-style: preserve-3d;
  margin-bottom: 10px;
}
.flip-button::before {
  font-family: "Roboto", "Font Awesome 5 Free";
  content: "\f107""\00a0""\00a0""\00a0"attr(btn-after) "\00a0""\00a0""\00a0""\f107";
  position: absolute;
  left: 0;
  top: 100%;
  width: 100%;
  height: 100%;
  color: black;
  line-height: 40px;
  transition: all 0.3s ease-out;
  background: white;
  transform: rotateX(-90deg);
  transform-origin: 50% 0;
}
.flip-button:hover {
  transform: rotateX(90deg) translateY(-10px);
}
.footer-container{
  background-color: #2565AE;
  max-height: 95%;
}
.link-container{
  display:flex;
  justify-content: space-evenly;
  max-width: 20%;
  margin-left: 40%;
}
.card {
  width: 100%;
  height: 100%;
}
.card-img-top {
  min-width: 100%;
  height: 25vw;
  object-fit: cover;
}
.glow {
  animation: glow 1s ease-in-out infinite alternate;
}
@keyframes glow {
  from {
    text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px rgb(129, 129, 129),
      0 0 40px rgb(129, 129, 129), 0 0 50px rgb(129, 129, 129),
      0 0 60px rgb(129, 129, 129);
  }
  to {
    text-shadow: 0 0 20px rgb(192, 192, 192), 0 0 30px rgb(192, 192, 192),
      0 0 40px rgb(192, 192, 192), 0 0 50px #fff, 0 0 60px #fff, 0 0 70px #fff;
  }
}
.gradient-custom {
  /* fallback for old browsers */
  background: #30cfd0;

  /* Chrome 10-25, Safari 5.1-6 */
  background: -webkit-linear-gradient(
    to right,
    rgba(48, 207, 208, 0.5),
    rgba(51, 8, 103, 0.5)
  );

  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background: linear-gradient(
    to right,
    rgba(48, 207, 208, 0.5),
    rgba(51, 8, 103, 0.5)
  );
}
.linear-wipe {
  margin-top: 50px;

  background-image: linear-gradient(
    to right,
    #00f260,
    #f79d00,
    #e2e605,
    #64f38c
  );
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;

  background-size: 300%;
  background-position: -100%;

  animation: animatedText 5s infinite alternate-reverse;
}
@keyframes animatedText {
  to {
    background-position: 100%;
  }
}
.mask-custom {
  backdrop-filter: blur(15px);
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 3em;
  border: 2px solid rgba(255, 255, 255, 0.1);
  background-clip: padding-box;
  box-shadow: 10px 10px 10px rgba(46, 54, 68, 0.03);
}
.progress {
  background: none;
  margin: 0 auto;
  box-shadow: none;
  width: 150px;
  height: 150px;
  line-height: 150px;
  position: relative;
}
.progress:after {
  content: "";
  border-radius: 50%;
  border: 15px solid #f2f5f5;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}
.progress > span {
  position: absolute;
  top: 0;
  z-index: 1;
  width: 50%;
  height: 100%;
  overflow: hidden;
}
.progress .progress-left {
  left: 0;
}
.progress .progress-bar {
  border-width: 15px;
  border-style: solid;
  position: absolute;
  width: 100%;
  height: 100%;
  background: none;
  top: 0;
}
.progress .progress-left .progress-bar {
  left: 100%;
  border-top-right-radius: 80px;
  border-bottom-right-radius: 80px;
  border-left: 0;
  -webkit-transform-origin: center left;
  transform-origin: center left;
}
.progress .progress-right {
  right: 0;
}
.progress .progress-right .progress-bar {
  left: -100%;
  -webkit-transform-origin: center right;
  transform-origin: center right;
  border-top-left-radius: 80px;
  border-bottom-left-radius: 80px;
  border-right: 0;
  animation: loading-1 1.8s linear forwards;
}
.progress .progress-value {
  font-size: 24px;
  color: black;
  background-color: white;
  text-align: center;
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #3C99DC;
}
.progress.ninety .progress-bar {
  border-color: rgb(0, 0, 255);
}
.progress.ninety .progress-left .progress-bar {
  animation: loading-2 1.5s linear forwards 1.8s;
}
.progress.seventyFive .progress-bar {
  border-color: rgb(0, 63, 255);
}
.progress.seventyFive .progress-left .progress-bar {
  animation: loading-3 1s linear forwards 1.8s;
}
.progress.sixty .progress-bar {
  border-color: rgb(0, 126, 255);
}
.progress.sixty .progress-left .progress-bar {
  animation: loading-4 0.4s linear forwards 1.8s;
}
.progress.fifty .progress-bar {
  border-color: rgb(0, 189, 255);
}
.progress.fifty .progress-left .progress-bar {
  animation: loading-1;
}
.shape {
  height: 250px;
  width: 250px;
  background-color: #0074d9;
  border-radius: 30px;
  margin-bottom: 30px;
  margin-right: 30px;
}

/* ID selectors */

#skills{
  background-color: #3C99DC;
}

#about{
  background-color: #66D3FA;
  display: flex;
  justify-content: center;
  height: 150px;
}

